tings Color Background Our App\r\n    =======================================================\r\n    =======================================================*/\r\n  --cs-background-primary: #fff;\r\n  --cs-background-secondary: #F5F5F5;\r\n  --cs-background-accent: #FB5135;\r\n  --cs-background-dark: #7f8490;\r\n  --cs-background-border: #C7C7CC;\r\n\r\n  /* Settings Color Text Our App\r\n    =======================================================\r\n    =======================================================*/\r\n  --cs-text-primary: #30384C;\r\n  --cs-text-secondary: #7F8490;\r\n  --cs-text-light: #ffffff;\r\n  --cs-text-accent: #FB5135;\r\n\r\n  /* Settings Size Avatar\r\n    =======================================================\r\n    =======================================================*/\r\n  --size-avatar: 72px;\r\n\r\n  /* Settings Size Thumbnail\r\n    =======================================================\r\n    =======================================================*/\r\n  --size-thumbnail: 120px;\r\n\r\n  /* Settings Border Radius\r\n    =======================================================\r\n    =======================================================*/\r\n  --border-radius: 5px;\r\n\r\n  /* Settings Color Icon\r\n    =======================================================\r\n    =======================================================*/\r\n  --cs-icon-color-primary: #FB5135;\r\n  --cs-icon-color-secondary: #30384C;\r\n  --cs-icon-color-light: #fff;\r\n}\r\n\r\n\r\n/* INCLUDE FONTS\r\n=======================================================\r\n=======================================================*/\r\n@import \"fontello\";\r\n@import \"comfortaa\";\r\n@import \"rtl\";\r\n@import \"animation\";\r\n\r\n/* INCLUDE FONTS AND COLOR COMBINATION\r\n=======================================================\r\n=======================================================*/\r\n@import \"themes-color/sunset-orange\";\r\n@import \"themes-color/mocha\";\r\n@import \"themes-color/seagull\";\r\n@import \"themes-color/turquoise\";\r\n@import \"themes-color/bull-shot\";\r\n@import \"themes-color/olive-drab\";\r\n@import \"themes-color/flush-orange\";\r\n@import \"themes-color/my-sin\";\r\n@import \"themes-color/pastel-green\";\r\n@import \"themes-color/emerald\";\r\n\r\n\r\n//////////////////////////////////////////////////\r\n// Theme: Ionic 4 UI Theme/Template App - Material Design - Orange-Light\r\n// Web Site: https://csform.com/\r\n// Author: CSFORM\r\n// Email support:  dev@csform.com\r\n//////////////////////////////////////////////////\r\n\r\n/* Setting Avatar\r\n=======================================================\r\n=======================================================*/\r\nion-avatar {\r\n  height: var(--size-avatar);\r\n  width: var(--size-avatar);\r\n}\r\n\r\n.ios {\r\n  ion-avatar {\r\n    margin: 8px 0;\r\n  }\r\n}\r\n\r\n/* Setting Thumbnail\r\n=======================================================\r\n=======================================================*/\r\nion-thumbnail {\r\n  --border-radius: 5px;\r\n  height: var(--size-thumbnail);\r\n  width: var(--size-thumbnail);\r\n}\r\n.ios {\r\n  ion-thumbnail {\r\n    margin: 8px 0;\r\n  }\r\n}\r\n\r\n/* SETTINGS ION-HEADER\r\n=======================================================\r\n=======================================================*/\r\n\r\n.header-md:after {\r\n  background-image: none !important;\r\n}\r\n\r\n/* SETTINGS TOOLBAR\r\n=======================================================\r\n=======================================================*/\r\nion-toolbar {\r\n  --background: var(--cs-background-primary, #fff);\r\n  --color: var(--cs-text-accent, #FB5135);\r\n  --border-color: transparent;\r\n\r\n  [slot=start] {\r\n    margin-inline-end: 0;\r\n  }\r\n\r\n  .ios {\r\n    ion-back-button,\r\n    ion-menu-button {\r\n      --color: var(--cs-text-accent, #FB5135);\r\n    }\r\n  }\r\n\r\n  ion-title {\r\n    font-weight: 400 !important;\r\n    font-size: var(--h3-font-size, 17px) !important;\r\n  }\r\n}\r\n\r\nion-item {\r\n  --ripple-color: transparent;\r\n}\r\n\r\n.margin-half {\r\n  margin: 8px;\r\n}\r\n\r\n.padding-half {\r\n  padding: 8px;\r\n}\r\n\r\nbody {\r\n  --ion-font-family: 'Comfortaa', cursive;\r\n}\r\n\r\n/* SETTINGS FONT SIZE\r\n=======================================================\r\n=======================================================*/\r\n\r\n.text-size-xl {\r\n  font-size: var(--h1-font-size, 24px) !important;\r\n  line-height: 29px;\r\n  letter-spacing: 1px;\r\n}\r\n\r\n.text-size-lg {\r\n  font-size: var(--h2-font-size, 22px) !important;\r\n  line-height: 27px;\r\n  letter-spacing: 1px;\r\n}\r\n\r\n.text-size-md {\r\n  font-size: var(--h3-font-size, 17px) !important;\r\n  line-height: 22px;\r\n  letter-spacing: 1px;\r\n}\r\n\r\n.text-size-xs {\r\n  font-size: var(--h4-font-size, 15px) !important;\r\n  line-height: 20px;\r\n  letter-spacing: 1px;\r\n}\r\n\r\n.text-size-sm {\r\n  font-size: var(--h5-font-size, 12px) !important;\r\n  line-height: 18px;\r\n  letter-spacing: 1px;\r\n}\r\n\r\n.text-size-p {\r\n  font-size: var(--text-font-size, 14px) !important;\r\n  line-height: 20px;\r\n  letter-spacing: 1px;\r\n}\r\n\r\n.text-nowrap {\r\n  text-overflow: ellipsis;\r\n  overflow: hidden;\r\n}\r\n\r\n/* SETTINGS FONT WEIGHT\r\n=======================================================\r\n=======================================================*/\r\n\r\n.font-light {\r\n  font-weight: 300 !important;\r\n}\r\n\r\n.font-regular {\r\n  font-weight: 400 !important;\r\n}\r\n\r\n.font-medium {\r\n  font-weight: 500 !important;\r\n}\r\n\r\n.font-bold {\r\n  font-weight: 700 !important;\r\n}\r\n\r\n/* SETTINGS COLOR\r\n=======================================================\r\n=======================================================*/\r\n\r\n.text-color-primary {\r\n  color: var(--cs-text-primary, #30384C) !important;\r\n}\r\n\r\n.text-color-secondary {\r\n  color: var(--cs-text-secondary, #7F8490) !important;\r\n}\r\n\r\n.text-color-accent {\r\n  color: var(--cs-text-accent, #FB5135) !important;\r\n}\r\n\r\n.text-color-light {\r\n  color: var(--cs-text-light, #ffffff) !important;\r\n}\r\n\r\n/* SETTINGS COLOR BACKGROUND OUR APP\r\n=======================================================\r\n=======================================================*/\r\nion-page {\r\n  background: var(--cs-background-primary, #fff);\r\n}\r\n\r\nion-content {\r\n  --background: var(--cs-background-primary, #fff);\r\n}\r\n\r\nion-list {\r\n  padding-top: 0 !important;\r\n}\r\n\r\n/* SETTINGS ITEM SLIDING\r\n=======================================================\r\n=======================================================*/\r\n\r\nion-item-option {\r\n  --background: var(--cs-background-secondary, #F5F5F5);\r\n  width: auto;\r\n}\r\n\r\n/* SETTINGS ITEM\r\n=======================================================\r\n=======================================================*/\r\n\r\nion-item {\r\n  --border-color: var(--cs-background-border);\r\n}\r\n\r\n/* SETTINGS MAIN MENU\r\n=======================================================\r\n=======================================================*/\r\n\r\n.ion-menu-toggle,\r\nion-menu-toggle {\r\n  .icon-small {\r\n    margin-right: 10px;\r\n  }\r\n\r\n  ion-label {\r\n    overflow: hidden !important;\r\n\r\n    h1 {\r\n      white-space: nowrap;\r\n      overflow: hidden;\r\n      text-overflow: ellipsis;\r\n    }\r\n  }\r\n}\r\n\r\n/* SETTINGS COLOR AND SIZE ICON\r\n=======================================================\r\n=======================================================*/\r\n.icon-small {\r\n  color: var(--cs-icon-color-primary, #FB5135);\r\n  font-size: var(--icon-size-small, 17px);\r\n}\r\n\r\n.icon-medium {\r\n  color: var(--cs-icon-color-primary, #FB5135);\r\n  font-size: var(--icon-size-medium, 22px);\r\n}\r\n\r\n.icon-large {\r\n  color: var(--cs-icon-color-secondary);\r\n  font-size: var(--icon-size-large, 32px);\r\n}\r\n\r\n\r\n/* SETTINGS BUTTON SIZE\r\n=======================================================\r\n=======================================================*/\r\n\r\n.default-button {\r\n  --color: var(--cs-text-light, #ffffff) ;\r\n  --background: var(--cs-background-accent, #FB5135) !important;\r\n  --padding-top: 12px;\r\n  --padding-bottom: 12px;\r\n  --padding-start: 20px;\r\n  --padding-end: 20px;\r\n  --border-radius: 5px;\r\n  --box-shadow: none;\r\n  font-size: var(--h4-font-size, 15px);\r\n  font-weight: bold;\r\n  height: auto;\r\n  --background-hover: var(--cs-background-accent, #FB5135) !important;\r\n  --color-hover:  var(--cs-text-light, #ffffff);\r\n  --color-activated: var(--cs-text-light, #ffffff);\r\n  --color-focused: var(--cs-text-light, #ffffff);\r\n}\r\n\r\n.button-icon {\r\n  --background: var(--cs-background-primary, #ffffff) !important;\r\n  --color: var(--cs-text-primary, #30384C) !important;\r\n  --border-radius: 5px !important;\r\n  --box-shadow: none;\r\n  font-size: var(--h4-font-size, 15px) !important;\r\n  font-weight: bold !important;\r\n  --background-hover: var(--cs-background-primary, #ffffff) !important;\r\n  --color-hover: var(--cs-text-primary, #30384C);\r\n  --color-activated: var(--cs-text-primary, #30384C)!important;\r\n  --color-focused: var(--cs-text-primary, #30384C)!important;\r\n\r\n  [slot=start] {\r\n    margin-inline-end: 8px !important;\r\n  }\r\n}\r\n\r\n.clear-button {\r\n  --background: transparent !important;\r\n  --color: var(--cs-text-primary, #30384C)!important;\r\n  --border-radius: 5px !important;\r\n  --padding-top: 5px;\r\n  --padding-bottom: 5px;\r\n  --padding-start: 10px;\r\n  --padding-end: 10px;\r\n  --box-shadow: none;\r\n  font-size: var(--h3-font-size, 17px) !important;\r\n  font-weight: bold !important;\r\n  height: auto;\r\n  --background-hover: transparent !important;\r\n  --color-hover: var(--cs-text-primary, #30384C)!important;\r\n  --color-activated: var(--cs-text-primary, #30384C)!important;\r\n  --color-focused: var(--cs-text-primary, #30384C)!important;\r\n}\r\n\r\n.button-action-shit {\r\n  --border-radius: 50% !important;\r\n  min-height: 40px !important;\r\n  min-width: 40px;\r\n  --box-shadow: none !important;\r\n  --background: transparent !important;\r\n  --background-hover: transparent !important;\r\n\r\n  ion-icon {\r\n    font-size: var(--h1-font-size, 24px);\r\n    color: var(--cs-icon-color-light);\r\n  }\r\n}\r\n\r\n  ion-button {\r\n    --ripple-color: transparent !important;\r\n    --color-focused: transparent;\r\n  }\r\n\r\n  ion-ripple-effect {\r\n    color: transparent;\r\n  }\r\n\r\n/* SETTINGS INPUT FIELD\r\n=======================================================\r\n=======================================================*/\r\n\r\n.input {\r\n  .icon-small {\r\n    color: red;\r\n  }\r\n}\r\n/* SETTINGS CHECK BOX\r\n=======================================================\r\n=======================================================*/\r\n\r\nion-checkbox {\r\n  --background-checked: var(--cs-background-accent, #FB5135);\r\n  --border-color-checked: var(--cs-background-accent, #FB5135);\r\n  --border-color: var(--cs-background-dark, #7f8490);\r\n  --border-radius: 4px;\r\n  --checkmark-color: var(--cs-text-light, #ffffff);\r\n}\r\n\r\n/* SETTINGS TOOGLE\r\n=======================================================\r\n=======================================================*/\r\n\r\nion-toggle {\r\n  --background: var(--cs-background-border, #C7C7CC);\r\n  --background-checked: var(--cs-background-accent, #FB5135);\r\n  --handle-background: var(--cs-background-primary, #fff);\r\n  --handle-background-checked: var(--cs-background-primary, #fff);\r\n  padding-inline-start: 4px;\r\n  padding-inline-end: 4px;\r\n}\r\n\r\n/* SETTINGS RADIO\r\n=======================================================\r\n=======================================================*/\r\n\r\nion-radio {\r\n  --color: var(--cs-background-dark, #7f8490);\r\n  --color-checked: var(--cs-background-accent, #FB5135);\r\n}\r\n/* SETTINGS TABS\r\n=======================================================\r\n=======================================================*/\r\n\r\nion-tab-bar {\r\n  --background: var(--cs-background-primary, #fff);\r\n}\r\n\r\nion-tab-button {\r\n  font-size: var(--h4-font-size, 15px);\r\n  --color: var(--cs-text-primary, #30384C);\r\n  --color-selected: var(--cs-text-accent, #FB5135);\r\n  --ripple-color: rgba(0, 0, 0, 0.1);\r\n\r\n  .ripple-effect {\r\n    display: none !important;\r\n  }\r\n}\r\n/* SETTINGS RANGE\r\n=======================================================\r\n=======================================================*/\r\n\r\nion-range {\r\n  --knob-background: var(--cs-background-accent, #fb5135);\r\n  --bar-background-active: var(--cs-background-accent, #fb5135);\r\n  --bar-background: var(--cs-background-border, #C7C7CC);\r\n  --pin-background: var(--cs-background-accent, #fb5135);\r\n}\r\n\r\n/* SETTINGS ION-BADGE\r\n=======================================================\r\n=======================================================*/\r\n\r\nion-badge {\r\n  --background: var(--cs-background-accent, #fb5135);\r\n  --color: var(--cs-text-light, #ffffff);\r\n  --padding-bottom: 5px;\r\n  --padding-end: 10px;\r\n  --padding-start: 10px;\r\n  --padding-top: 5px;\r\n  border-radius: 5px;\r\n  font-weight: 400;\r\n  font-size: var(--h4-font-size, 15px);\r\n}\r\n\r\n/* SETTINGS SEARCHBAR\r\n=======================================================\r\n=======================================================*/\r\n\r\nion-searchbar {\r\n  --background: #F3F3F3 !important;\r\n  --cancel-button-color: var(--cs-text-primary, #30384C) !important;\r\n  --clear-button-color: var(--cs-text-primary, #30384C) !important;\r\n  --color: var(--cs-text-primary, #30384C) !important;\r\n  --icon-color: var(--cs-text-primary, #30384C) !important;\r\n  --placeholder-color: var(--cs-text-primary, #30384C) !important;\r\n\r\n  .searchbar-input {\r\n    border-radius: 10px !important;\r\n    box-shadow: none !important;\r\n  }\r\n}\r\n/* SETTINGS SEGMENTS\r\n=======================================================\r\n=======================================================*/\r\n\r\nion-segment {\r\n  background: var(--cs-background-primary);\r\n}\r\n\r\nion-segment-button {\r\n  --background: transparent;\r\n  --background-checked: transparent !important;\r\n  --background-focused: transparent !important;\r\n  --background-hover: transparent !important;\r\n  --ripple-color: transparent !important;\r\n  --color: var(--cs-text-primary);\r\n  --color-checked: var(--cs-text-accent) !important;\r\n  --color-focused: var(--cs-text-primary) !important;\r\n  --color-hover: var(--cs-text-primary) !important;\r\n  --indicator-box-shadow: none;\r\n  --border-width: 0 !important;\r\n  --border-radius: 0;\r\n  font-size: 14px;\r\n  font-weight: 400;\r\n  margin: 5px;\r\n  min-width: unset;\r\n  max-width: unset;\r\n}\r\n\r\n//Setting for ios\r\n.ios {\r\n  ion-segment {\r\n    padding: 10px 0 0 0;\r\n    ion-segment-button {\r\n      --indicator-color: transparent;\r\n    }\r\n    .segment-button-checked {\r\n      border-bottom: 2px solid var(--cs-background-accent);\r\n    }\r\n  }\r\n}\r\n\r\n/* SETTINGS SUPER TAB\r\n=======================================================\r\n=======================================================*/\r\n\r\nsuper-tabs-toolbar {\r\n  &:after {\r\n    background-image: none !important;\r\n  }\r\n}\r\n\r\nsuper-tabs-container, super-tab {\r\n  width: 100%;\r\n}\r\n\r\nsuper-tab-button {\r\n  flex-grow: 1;\r\n  max-width: unset;\r\n}\r\n\r\n/* SETTINGS NEW-CATEGORY\r\n=======================================================\r\n=======================================================*/\r\n\r\n.new-category {\r\n  background-color: var(--cs-background-accent, #FB5135);\r\n  color: var(--cs-text-light, #ffffff);\r\n  font-size: var(--h4-font-size, 15px);\r\n  font-weight: 700;\r\n  line-height: 17px;\r\n  padding: 10px 20px;\r\n}\r\n\r\n/* SETTINGS WIZARD\r\n=======================================================\r\n=======================================================*/\r\n\r\n.swiper-pagination-bullet-active {\r\n  --bullet-background: var(--cs-background-accent, #FB5135) !important;\r\n  background: var(--bullet-background) !important;\r\n}\r\n\r\ncs-wizard-layout-1 {\r\n  .swiper-pagination.swiper-pagination-bullets {\r\n    bottom: 50px !important;\r\n  }\r\n}\r\n\r\ncs-wizard-layout-2 {\r\n  .swiper-pagination.swiper-pagination-bullets {\r\n    bottom: 0 !important;\r\n  }\r\n}\r\n\r\ncs-wizard-layout-3 {\r\n  .swiper-pagination.swiper-pagination-bullets {\r\n    bottom: 50px !important;\r\n  }\r\n}\r\n\r\ncs-wizard-layout-4 {\r\n  .swiper-pagination.swiper-pagination-bullets {\r\n    bottom: 10px !important;\r\n  }\r\n}\r\n\r\ncs-profile-layout-2 {\r\n  .swiper-pagination.swiper-pagination-bullets {\r\n    bottom: -35px !important;\r\n  }\r\n}\r\n\r\ncs-profile-layout-4 {\r\n  .swiper-pagination.swiper-pagination-bullets {\r\n    bottom: -25px !important;\r\n  }\r\n}\r\n\r\n/* Default Overview Ionic Section\r\n=======================================================\r\n=======================================================*/\r\n\r\n.background-size {\r\n  background-size: cover !important;\r\n  background-position: center !important;\r\n  background-repeat: no-repeat !important;\r\n}\r\n\r\n.background-white {\r\n  background-color: var(--cs-background-primary, #fff);\r\n}\r\n\r\n[slot=end] {\r\n  margin-inline-start: 0;\r\n}\r\n\r\n[slot=start] {\r\n  margin-inline-end: 16px;\r\n}\r\n\r\n.ios {\r\n  [slot=start] {\r\n    margin-inline-end: 16px;\r\n  }\r\n}\r\n\r\n.border-radius {\r\n  border-radius: 5px;\r\n}\r\n\r\n.no-border-radius {\r\n  border-radius: 0;\r\n}\r\n\r\n.no-box-shadow {\r\n  box-shadow: none !important;\r\n}\r\n\r\n.transparent {\r\n  --background: transparent;\r\n}\r\n\r\n.box-shadow {\r\n  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.2) !important;\r\n  position: relative;\r\n  z-index: 999;\r\n}\r\n\r\n.clearfix {\r\n  display: block;\r\n  clear: both;\r\n}\r\n\r\n/* Settings Spinner\r\n================================================\r\n================================================*/\r\nion-spinner {\r\n  width: 40px;\r\n  height: 40px;\r\n  color: var(--cs-background-accent, #FB5135) !important;\r\n}\r\n\r\ncs-spinner {\r\n  &.svg-size {\r\n    font-size: 45px;\r\n    color: var(--cs-background-accent, #FB5135) !important;\r\n    stroke: var(--cs-background-accent, #FB5135) !important;\r\n    fill: var(--cs-background-accent, #FB5135) !important;\r\n  }\r\n}\r\n/* Settings Reorder Group\r\n================================================\r\n================================================*/\r\nion-reorder-group {\r\n  ion-item {\r\n    --inner-padding-end: 0 !important;\r\n  }\r\n\r\n  ion-reorder {\r\n    display: flex !important;\r\n    align-items: center !important;\r\n    height: 100% !important;\r\n    background: var(--cs-background-secondary, #F5F5F5) !important;\r\n  }\r\n}\r\n/* Settings Picker\r\n================================================\r\n================================================*/\r\nion-picker {\r\n  .picker-button {\r\n    color: var(--cs-text-accent) !important;\r\n  }\r\n\r\n  .picker-opt {\r\n    color: var(--cs-text-primary) !important;\r\n  }\r\n\r\n  .picker-opt-selected {\r\n    color: var(--cs-text-accent) !important;\r\n  }\r\n}\r\n\r\n/* Settings Select\r\n================================================\r\n================================================*/\r\n\r\nion-select {\r\n  color: var(--cs-text-accent) !important;\r\n}\r\n\r\n/* Settings Alert\r\n================================================\r\n================================================*/\r\n\r\nion-alert {\r\n  .alert-wrapper {\r\n    .alert-head {\r\n      .alert-title {\r\n        font-size: var(--h4-font-size, 15px) !important;\r\n        font-weight: 300 !important;\r\n        color: var